import Guid from '../guid/Guid';

/**
 * Object that represents the data neccesary to build an SPWeb object.
 * @internal
 */
export interface ISPWebData  {
  absoluteUrl: string;
  id: string;
  serverRelativeUrl: string;
  title: string;
}

/**
  * This class is primarily used with the PageContext class.  It provides contextual
  * information for the SharePoint site ("web") that hosts the page.
  * TODO BUG 227874: Add validation to SPWeb and other PageContext objects
  * @public
  */
export default class SPWeb {
  private _absoluteUrl: string;
  private _id: Guid;
  private _serverRelativeUrl: string;
  private _title: string;

  /**
   * @internal
   */
  constructor(data: ISPWebData) {
    this._absoluteUrl = data.absoluteUrl || '';
    this._id = Guid.tryParse(data.id);
    this._serverRelativeUrl = data.serverRelativeUrl || '';
    this._title = data.title || '';
  }

  /**
   * The GUID that identifies the SPWeb on the server or undefined if the Guid string
   * value is invalid.
   */
  public get id(): Guid {
    return this._id;
  }

  /**
   * Returns the absolute URL for this SPWeb.
   * Example: "https://example.com/sites/PubSite/SubWeb"
   */
  public get absoluteUrl(): string {
    return this._absoluteUrl;
  }

  /**
   * Returns the server-relative URL for this SPWeb.
   * Example: "/sites/PubSite/SubWeb"
   */
  public get serverRelativeUrl(): string {
    return this._serverRelativeUrl;
  }

  /**
   * Returns the title of the SharePoint site.
   */
  public get title(): string {
    return this._title;
  }
}
